* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  background-color: #eee;
  justify-content: center;

  form {
    width: 95%;
    margin: auto;
    max-width: 560px;

    input {
      width: 100%;
      outline: none;
      margin: 1rem 0;
      padding: 12px;
      font-size: 15px;
      border-radius: 3px;
      border: 1px solid rgb(4, 28, 34);
    }
    input[type="submit"] {
      width: 100%;
      color: #fff;
      border: none;
      max-width: 200px;
      cursor: pointer;
      transition: 0.2s;
      background: rgb(34, 92, 44);
      &:hover {
        background: darken(rgb(34, 92, 44), 10%);
      }
    }

    p {
      color: red;
      margin: 0.5rem 0;
      font-size: 0.9rem;
      font-style: italic;
    }
  }
}
